@charset "UTF-8";
.main {
    section { width:100%; }
    .sec1 {
        height:768px; overflow:hidden;
        .video_wrap {
            width:100%; min-width:1580px;
            video { width:100%; height:100%; }
        }
        .txt_wrap {
            position:absolute; width:1580px; left:50%; bottom:150px;
            @include transform(translate(-50%, 0));
            h1 {
                width:fit-content; position:relative; font-size:160px; color:$white;
                i { position:absolute; font-size:32px; font-weight:$regular; right:-165px; top:34px; }
            }
            span { color:$white; font-size:30px; }
        }
    }
    .sec2 {
        height:1090px; background:url(../../images/main/sec1_img.jpg) 50% 50% no-repeat; background-size:cover;
        .txt_wrap {
            width:950px; margin:0 auto; text-align:center; padding:245px 0 0;
            & > span {
                display:block;
                &:nth-of-type(1) { padding:50px 0 0; color:#d2ad83; font-size:24px;}
                &:nth-of-type(2) { padding:40px 0 60px; color:$white; font-size:20px; }
            }
            h1 { padding:85px 0 0; color:#f0f0f0; font-size:50px; }
            p {
                color:#acb4bf; font-size:16px; line-height:38px;
                span { display:block; @include transform(skew(-0.03deg)); }
                i { font-family:$NanumSquare; }
            }
        }
    }
    // 가치
    .sec4 {
        background-color:$navy;
        height:980px;
        .cont_wrap {
            padding:80px 0 0; width:1580px; margin:0 auto; position:relative;
            .line {
                width:100%; height:1px; background-color:$white;
                position:absolute; top:151px; left:0;
                i {width:46px; height:1px;background-color:red;display:block;position:relative; left:20px;}
            }
        }
        .tab_menu {
            display:flex;justify-content:space-between;padding:0 155px 0 0;
            li.active {
                button {
                    color:$goldYellow;
                    span { color:$white; }
                }
            }
            button {
                font-family:$NanumSquare;text-align:left;font-size:14px;color:#75777c; font-weight:$bold;
                @include transform(skew(-0.03deg));
            }
            span {font-family:$Play; padding:8px 0 0; display:block; letter-spacing:.6px;font-weight:$regular;}
            em {padding:0 0 0 10px;font-family:$Play;font-weight:$regular;}
        }
        .tab_wrap {
            position:absolute; top:152px;
            width:100%; height:800px;
            background-color:#222;
            li {
                &:first-child { display:block; opacity:1; }
                position:absolute; left:0; top:0;
                font-size:100px;
                color:$white;
                display:none;

            }
        }
    }
    // 세대 선택하기
    .sec5 {
        background:url(../../images/main/sec5_img.jpg) 50% 50% no-repeat; height:980px;
        overflow:hidden; /*background-size:cover;*/position:relative;
        .generation_type {
            width:1920px; margin:0 auto;position:absolute;height:100px;top:0;left:50%;z-index:2;
            @include transform(translate(-50%, 0));
            .svg_wrap {
                position:relative; z-index:2;
                svg {width:100%; height:980px;}
                path,polygon {fill:transparent; position:relative;z-index:10;}
            }
            .info_wrap {
                position:absolute; left:0; top:0;
                width:100%; height:100vh; z-index:1;
                li {
                    position:absolute;
                    &.active {
                        &:nth-of-type(1), &:nth-of-type(2), &:nth-of-type(3), &:nth-of-type(4), &:nth-of-type(5) {
                            &:after {opacity:1;}
                        }
                        .plus_btn {
                            em {opacity:1;@include transform(rotate(90deg));}
                        }
                        .sub_info {opacity:1; display:block; @include transform(translate(0,-50%)); }
                    }
                    &:after {
                        width:100%; height:100%;content:"";display:block;position:absolute; top:0; left:0;
                        opacity:0;
                        @include transition(all, .4s);
                    }
                    &:nth-of-type(1) {
                        width:274px; height:219px; top:281px;left:54px;
                        &:after {
                            //background:url(../../images/main/A_generation_over.jpg) 0 0 no-repeat;
                        }
                        .plus_btn {top:65px; left:155px;}
                    }
                    &:nth-of-type(2) {
                        width:347px; height:248px; top:315px;left:528px;
                        &:after {
                            //background:url(../../images/main/A_generation_over.jpg) 50% 50% no-repeat;
                        }
                        .plus_btn {top:80px; left:210px;}
                    }
                    &:nth-of-type(3) {
                        width:519px; height:332px; top:483px; left:690px;
                        &:after {
                            background:url(../../images/main/C_generation_over.jpg) 50% 50% no-repeat;
                        }
                        .plus_btn {top:160px; left:295px;}
                    }
                    &:nth-of-type(4) {
                        width:411px; height:271px;
                        top:338px;left:1443px;
                        &:after {
                            //background:url(../../images/main/C_generation_over.jpg) 50% 50% no-repeat;
                        }
                        .plus_btn {top:150px; left:230px;}
                    }
                    &:nth-of-type(5) {
                        width:445px; height:240px;
                        top:275px;left:1085px;
                        &:after {
                            //background:url(../../images/main/C_generation_over.jpg) 50% 50% no-repeat;
                        }
                        .plus_btn {top:70px; left:240px;}
                    }
                }
                .plus_btn {
                    display:block; width:70px; height:70px; background-color:$gold; font-size:32px; color:$white;
                    font-weight:$extraBold; position:absolute; z-index:1; padding:20px 0; text-align:center;
                    @include boxSizing;
                    @include borderRadius(70px);
                    em {
                        width:100%; height:100%;position:absolute;left:0; top:0;background-color:$white;z-index:2;
                        opacity:0;
                        @include transition(all, .4s);
                        @include borderRadius(100%);
                        &:after, &:before {
                            content:"";display:block;position:absolute; top:50%; left:50%;overflow:hidden;
                            @include transform(translate(-50%,-50%));
                        }
                        &:after {width:4px; height:24px;background-color:$gold;}
                        &:before {width:24px; height:4px;background-color:$gold;}
                    }
                }
                .sub_info {
                    width:230px; height:306px;text-align:left; background-color:$navy;
                    position:absolute; top:50%; left:50%; opacity:0;
                    @include transition(all, .4s);
                    @include transform(translate(30px,-50%));
                    strong { display:block; padding:40px 0 0 40px; font-size:72px; color:#d2ad83; }
                    i {position:relative;left:7px;top:-3px; font-size:18px; color:#d2ad83; }
                    span {
                        display:block;
                        font-size:15px; font-family:$NanumSquare;
                        @include transform(skew(-0.03deg));
                    }
                    p { padding:15px 0 0; font-size:16px; font-weight:$regular; }
                    .bottom_txt {
                        text-align:right;
                        position:absolute; bottom:40px; right:30px;
                    }
                }
            }
        }
        .txt_wrap {
            width:1580px; position:absolute; bottom:100px; left:50%;
            @include transform(translate(-50%, 0));
            h1 { display:block; font-size:54px; color:$white; font-weight:$regular; padding:30px 0 0; }
            p { font-size:14px; color:$white; }
        }
    }
    .sec6 {
        width:100%; background-color:$navy;
        img {
            padding:170px 0 0;
            width:103px; height:103px;
        }
        .txt_wrap {
            width:950px;margin:0 auto; text-align:center; padding:0 0 220px;
            p { padding:50px 0 0; color:$goldYellow; font-size:24px; }
            h1 {
                padding:30px 0 75px;
                font-size:50px; color:$white;
            }
            span {color:$white; font-size:18px; display:block; }
            strong {
                display:block;
                padding:35px 0 0;
                font-size:72px; color:$goldYellow; }
        }
    }
}
.sub1 {
    width:100%; height:3000px; position:relative; background-color:#eee;
    p { position:absolute; right:300px; top:300px; font-size:100px; }
    .box {
        width:500px; height:500px; position:absolute; top:900px; left:50%; overflow:hidden;
        &.box2 { width:500px; height:500px; top:1200px; left:10%; }
    }
    .button_wrap {
        position:absolute; left:50%; background-color:#ccc; top:0;
        span {
            position:absolute; top:48%; left:50%;
            @include transform(translate(-50%, -50%));
        }
        button {
            &:hover {
                span { color:red; }
                svg {
                    @include transform(rotate(90deg));
                    path { stroke-dashoffset:0; }
                }
            }
        }
        svg {
            width:80px; height:80px;
            @include transition(all, .8s);
            path {
                width:100%; stroke-dasharray:390; stroke-dashoffset:-390;
                @include transition(all, .8s);
            }
        }
    }
}

